import React from "react";

import { useStaticQuery, graphql } from "gatsby";

import LandingItem from "./LandingItem";

import * as styles from './LandingContainer.module.css'

const LandingContainer = (props) => {

    const data = useStaticQuery(graphql`
    query landingpage {
      allStrapiLandingCardImage {
        nodes {
          name
          image {
            localFile {
              childImageSharp {
                gatsbyImageData
              }
            }
          }
          title
          description {
            data {
              description
            }
          }
        }
      }
    }
    `)

    return (
        <div className={styles.container}>
            {data.allStrapiLandingCardImage.nodes.map(data => <LandingItem data={data}/> )}            
        </div>
    )
}

export default LandingContainer;