import React from "react";
import {Link} from 'gatsby'

import { GatsbyImage, getImage } from "gatsby-plugin-image";
import * as styles from './LandingItem.module.css';

import Reactmarkdown from "react-markdown";


const LandingItem = ({data}) => {
    
    return (
        <div className={`${styles.card} ${styles[data.name]}`}>
            <GatsbyImage
                image={getImage(data.image.localFile)} 
                className={styles.image}
            />
            <div className={styles.info}>
                <h2>{data.title}</h2>
                
                <Reactmarkdown children={data.description.data.description} />

                <Link to={data.name} className={`${styles.button}`}>Click me</Link>
            </div>

        </div>
    )
}

export default LandingItem;